.App {
  text-align: center;
  background-color: #071611;
  top: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-top: 5vh;
  margin-bottom: 5vh;
  border-radius: 50%;
  box-shadow: cornsilk;
}

.App-header {
  margin-left: 1vw;
  margin-right: 1vw;
  margin-bottom: 2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
